/* font-family: 'Open Sans', sans-serif;
font-family: 'Open Sans Condensed', sans-serif; */

* {
    padding: 0;
    margin: 0;
    font-family: "Open Sans Condensed", sans-serif;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
}
a {
    text-decoration: none;
}
body {
    background-color: #333;
    // width: 1920px;
}
.main-title,
.main-title span{
    padding-bottom: 101px;
    font-weight: 300;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-size: 48px;
}
.about-the-theater__title span,
.productions__title span,
.footer__title span,
.partners__title span {
    color: #d1a954;
}
.header {
    display: flex;
    justify-content: center;
    padding-top: 52px;
    z-index: 10;
    position: relative;
}
.header__link:last-child {
    padding-right: 0px;
}
.header__link {
    padding-right: 66px;
    font-size: 18px;
    letter-spacing: 0.1em;
}
.header__phone {
    font-size: 16px;
    letter-spacing: 0.05em;
    border-bottom: 2px solid #d1a954;
    padding-bottom: 4px;
}
.playbill {
    position: relative;
    padding-top: 267px;
    padding-bottom: 208px;
    margin-bottom: 66px;
    top: -78px;
    z-index: 9;
    display: flex;
    justify-content: center;
    background-image: url(../img/playbill-background.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.playbill__information-about-the-theater {
    padding-right: 125px;
    align-self: center;
}
.playbill__title {
    font-size: 64px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    padding-bottom: 45px;
}
.playbill__subtitle {
    padding-bottom: 43px;
    max-width: 540px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-transform: none;
}
.playbill__button {
    background-color: #d1a954;
    padding: 10px 47px;
    font-size: 18px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
}
.playbill__productions {
    width: 374px;
}
.playbill__container-img {
    position: relative;
    margin-bottom: 55px;
}
.playbill__img {
    position: relative;
    z-index: 10;
}
.playbill__img--background {
    position: absolute;
    left: 24px;
    top: 23px;
    z-index: 9;
}
.playbill__buttons {
    display: flex;
    justify-content: space-between;
}
.playbill__button-nav {
    font-size: 18px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #c4c4c4;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
.playbill__button-nav--left i {
    padding-right: 11px;
}
.playbill__button-nav--right i {
    padding-left: 11px;
}
.about-the-theater__detailed-information {
    display: flex;
    justify-content: center;
    padding-bottom: 123px;
}
.about-the-theater__text {
    max-width: 445px;
    padding-right: 30px;
}
.about-the-theater__statistics {
    padding-right: 60px;
}
.about-the-theater__text-title,
.about-the-theater__statistics-title {
    padding-bottom: 35px;
    font-size: 21px;
    letter-spacing: 0.1em;
}
.about-the-theater__text-subtitle {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-transform: none;
}
.about-the-theater__statistics-text,
.about-the-theater__statistics-text span {
    font-size: 16px;
    letter-spacing: 0.05em;
}
.about-the-theater__statistics-text span {
    color: #d1a954;
}
.about-the-theater__statistics-img {
    padding-bottom: 15px;
}
.about-the-theater__statistics-elem {
    padding-bottom: 14px;
}
.productions__container-list {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 150px;
}
.productions__elem-img {
    width: 100%;
}
.productions__elem-title {
    font-size: 24px;
    letter-spacing: 0.05em;
    font-weight: 400;
    position: absolute;
    top: 40px;
    left: 40px;
}
.productions__elem-data {
    font-size: 18px;
    letter-spacing: 0.1em;
    position: absolute;
    left: -6px;
    bottom: 88px;
    transform: rotate(-90deg);
}
.productions__elem {
    position: relative;
}
.achievement {
    display: flex;
    justify-content: center;
    background-image: url(../img/achievement-theatre.png);
    background-repeat: no-repeat;
    background-position: center;
    padding: 133px;
    margin-bottom: 140px;
}
.achievement__years {
    padding-right: 103px;
}
.achievement__years p {
    font-size: 72px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #d1a954;
}
.achievement__icon-star {
    padding-bottom: 25px;
}
.achievement__icon-star img {
    padding-right: 15px;
}
.achievement__comment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.achievement__text {
    font-size: 36px;
    text-transform: none;
    font-family: "Open Sans", sans-serif;
}

.partners__img {
    padding: 39px;
}
.partners__container-el {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 900px;
    margin: 0 auto;
}
.partners {
    margin-bottom: 82px;
}
.footer__container-contacts {
    display: flex;
    justify-content: center;
    position: relative;
    padding-bottom: 80px;
}
.footer__contacts {
    margin-right: 125px;
}
.footer__contact-el {
    font-size: 16px;
    letter-spacing: 0.05em;
    font-weight: 400;
    padding-bottom: 20px;
    font-weight: bold;
    white-space: nowrap;
}
.footer__container-text-fields {
    display: flex;
}
.footer__input {
    width: 152px;
    height: 17px;
    margin-right: 30px;
    margin-bottom: 30px;
    background-color: transparent;
    border: 1px solid #616161;
    outline: none;
    padding: 10px 15px;
}
.footer__textarea {
    width: 280px;
    height: 86px;
    background-color: transparent;
    border: 1px solid #616161;
    outline: none;
    padding: 10px 15px;
}
.footer__container-input {
    width: 432px;
}
.footer__privacy-policy {
    width: 396px;
    height: 50px;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 150%;
    color: #989898;
}
.footer__container-send-button {
    display: flex;
    justify-content: space-between;
}
.footer__button-form {
    font-family: "Open Sans Condensed", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    padding: 10px 30px;
    background: #d1a954;
    border: none;
}
.watermark {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
    border-top: 1px solid #565656;
    margin: 0 auto;
}
.watermark__politics,
.watermark__developed {
    color: #989898;
    font-family: "Open Sans", sans-serif;
    font-weight: normal;
    font-size: 12px;
}

.popup-thanks-top-of-the-page {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #2cf11f;
    opacity: 0;
    transition: 0.5s;
    white-space: nowrap;
}
.popup-thanks-top-of-the-page__text {
    font-size: 18px;
    font-weight: bold;
    padding: 7px 35px;
    margin: 0;
}
.popup-thanks-top-of-the-page--active {
    opacity: 1;
}
.popup-thanks-center-of-the-page {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #333333;
    padding: 31px;
    max-width: 571px;
    box-shadow: 0 4px 15px #8f8f8f;
}
.popup-thanks-center-of-the-page--active {
    display: flex;
    flex-direction: column;
    z-index: 11;
}
.popup-thanks-center-of-the-page__text {
    font-size: 18px;
    padding-bottom: 20px;
}
.popup-thanks-center-of-the-page__link,
.popup-thanks-center-of-the-page__text a {
    color: #0056b3;
}
.popup-thanks-center-of-the-page__close-button {
    position: absolute;
    right: 11px;
    top: 12px;
    border: none;
    background: none;
    outline: none !important;
    cursor: pointer;
}
@media screen and (max-width: 576px) {
    .popup-thanks-center-of-the-page {
        min-width: 293px;
    }
}
